<template>
    <v-container grid-list-md fluid>
        <v-layout row wrap>
            <v-flex md6>
                <v-layout row>
                    <component-p-e-r v-if="this.$store.state.user.nivel_acceso===0"/>
                    <component-estacion v-if="this.$store.state.user.nivel_acceso===0"/>
                    <v-btn color="info" flat
                           title="Actualizar estaciones"
