                    </v-btn>
                    <!--<v-btn color="info" flat
                           title="Nuevo Kit"
                           @click="create"
                           class="square">
                        <v-icon>devices_other</v-icon>
                    </v-btn>-->
                    <v-text-field
                            color="blue darken-4"
