                        :search="search"
                >
                    <template v-slot:items="props">
                        <tr :active="props.selected" @click="selectedItem(props.item)">
                            <td>{{ props.item.nro_estacion}}</td>
                            <td>{{ props.item.notario ? props.item.notario.nombre:'' }}</td>
                        </tr>
                    </template>
                </v-data-table>
            </v-flex>
            <v-flex md6>
                <v-btn color="info" :disabled="!estacion"
                       @click="printDoc()"
                       flat
                       class="square"
                       title="Imprimir">
                    <v-icon >print</v-icon>
                </v-btn>
                <v-layout row>
                    <!--<v-text-field
                            color="blue darken-4"
                            background-color="blue lighten-5"
                            class="pt-0 pr-1 pl-1"
                            label="Notario"
                            single-line
                            hide-details
                            append-icon="person"
                            @keyup.enter="addEquipoToEstacion($event.target.value)"
                    ></v-text-field>-->
                    <v-autocomplete
                            v-model="autocomplete.model"
                            :items="items"
                            :loading="autocomplete.isLoading"
                            :search-input.sync="autocomplete.search"
                            :filter="customFilter"
                            @change="assignNotario"
                            hide-no-data
                            item-text="nombre"
                            item-value="id"
                            label="Public APIs"
                            placeholder="Comience a escribir para buscar al notario"
                            single-line
                            hide-details
                            class="pt-0 pr-1 pl-1"
                            color="blue darken-4"
                            background-color="blue lighten-5"
                            return-object
                            clearable
                            open-on-clear
                    >
                        <template v-slot:item="{ item, index }">
                            <v-layout align-center justify-space-between fill-height>
                                <v-flex xs8>
                                    <span class="v-text-field--full-width">{{item.nombre }}</span>
                                </v-flex>
                                <v-flex class="text-xs-right">
                                    <span>{{item.ci }}</span>
                                </v-flex>
                            </v-layout>
                        </template>
                    </v-autocomplete>
                </v-layout>
                <v-layout row>
                    <v-text-field
                            color="blue darken-4"
                            background-color="blue lighten-5"
                            class="pt-0 pr-1 pl-1"
                            label="Introduzca el número de serie del equipo"
                            single-line
                            hide-details
                            append-icon="laptop_windows"
                            @keyup.enter="addEquipoToEstacion($event.target.value)"
                    ></v-text-field>
                </v-layout>
                <v-data-table

                        disable-initial-sort
                        :headers="dataTableEquipos.headers"
                        :items="dataTableEquipos.equipos"
                >
                    <template v-slot:items="props">

                        <td>
                            <v-btn color="error" flat
                                   title="Quitar"
                                   @click="removeEquipoOfEstacion(props.item)"
                                   class="square">
                                <v-icon>remove</v-icon>
                            </v-btn>
                            {{ props.item.nombre}} {{ props.item.marca}}
                        </td>
                        <td>{{ props.item.nro_serie}}</td>
                        <td width="25%">
                            <v-select :items="estados"
                                      v-model="props.item.estado"
                                      @change="changeEstadoEquipo(props.item)"
                                      flat
                                      hide-details
                                      dense
                                      color="blue darken-4"
                                      background-color="blue lighten-5"
                                      item-text="state"
                                      item-value="abbr"
                                      label="Estado"
                                      single-line
                                      solo
                            ></v-select>
                            <!--<span v-if="props.item.estado === 'b'">Bien</span>
                            <span v-if="props.item.estado === 'r'">En reparación</span>
                            <span v-if="props.item.estado === 'd'">De baja</span>-->
                        </td>
                    </template>
                </v-data-table>
            </v-flex>
        </v-layout>
        <component-print-asignacion :print-data="printEstacion" ref="componentPrintProEleRef"/>
    </v-container>
</template>

<script>
    import axios from 'axios';
    import ComponentPER from '../components/ComponentProcesosElectoralesReferendo.vue';
    import ComponentEstacion from '../components/ComponentEstacion.vue';
    import Equipo from '../models/Equipo';
    import ComponentPrintAsignacion from "../components/ComponentPrintAsignacion.vue";

    export default {

        components: {
            ComponentPER,
            ComponentEstacion,
            ComponentPrintAsignacion
        },
        mounted() {
            this.$nextTick(() => {
                this.getEstaciones();
            })
        },
        data: () => {
            return {
                printEstacion:null,
                autocomplete:{
                    descriptionLimit: 65,
                    entries: [],
                    isLoading: false,
                    model: null,
                    search: null,
                },
                dataTable: {
                    headers: [
                        {
                            text: 'Numero de estación',
                            align: 'left',
                            //sortable: false,
                            value: 'nro_estacion'
                        },{
                            text: 'Notario',
                            align: 'left',
                            //sortable: false,
                            value: 'notario.nombre'
                        },
                    ],
                    estaciones: [],
                },
                dataTableEquipos: {
                    headers: [
                        {
                            text: 'Descripción',
                            align: 'left',
                            //sortable: false,
                            value: 'descripcion'
                        },
                        {text: 'Número de serie', value: 'nro_serie'},
                        {text: 'Estado', value: 'estado'},
                    ],
                    equipos: [],
                },
                selected: [],
                estacion: null,
                nro_serie:null,
                search: '',
                estados: [
                    {state: 'Bien', abbr: 'b'},
                    {state: 'En Reparación', abbr: 'e'},
                    {state: 'De baja', abbr: 'd'},
                ],
                equipo: new Equipo()
            }
        },
        methods: {
            editTecnico(tecnico) {
                this.selectedTecnico = tecnico;
                this.$nextTick(() => {
                    this.$refs.componentTecnico.openDialog();
                });
            },
            editNotario(notario) {
                this.selectedNotario = notario;
                this.$nextTick(() => {
                    this.$refs.componentNotario.openDialog();
                });
            },
            /*create() {
                let _confirm = confirm('Está seguro !!!');
                if (_confirm) {
                    axios.post(this.$urlApi.resourcesKit
                    ).then(response => {
                        this.$toastr('success', '', 'TAREA REALIZADA CON ÉXITO');
                        this.getKits();
                        this.$emit('register-success');
                    }).catch(errors => {
                        this.$notifyErrors(errors);
                    });
                }
            },*/
            getEstaciones() {
                axios.get(this.$urlApi.resourcesEstacion
                ).then(response => {
                    this.dataTable.estaciones = response.data;
                }).catch(errors => {
                    console.log('error de servidor')
                });
            },
            getEquiposByEstacion() {
                axios.get(this.$urlApi.getEquiposByEstacion+this.estacion.id
                ).then(response => {
                    this.dataTableEquipos.equipos = response.data;
                }).catch(errors => {
                    console.log('error de servidor')
                });

            },
            printDoc(){
                this.printEstacion = this.estacion;
                this.printEstacion.equipos = this.dataTableEquipos.equipos;
                this.$nextTick(()=>{
                    this.$refs.componentPrintProEleRef.printAsignacion();
                });
                /*axios.get(this.$urlApi.getEquiposByEstacion+this.estacion.id
                ).then(response => {

                }).catch(errors => {
                    console.log('error servidor')
                }).then(response => {
                });*/
            },
            removeEquipoOfEstacion(equipoEstacion) {
                let _confirm = confirm('¿Está seguro?');
                if(_confirm) {
                    let inputs={
                        id_estacion:equipoEstacion.id_kit,
                        id_equipo:equipoKit.id_equipo,
                    };
                    axios.patch(this.$urlApi.removeEquipoOfKit, inputs
                    ).then(response => {
                        this.getEquiposByKit()
                    }).catch(errors => {
                        console.log('error de servidor')
                    });
                }
            },
            addEquipoToEstacion(nro_serie) {
                if(this.estacion){
                    let inputs ={
                        id_estacion: this.estacion.id,
                        nro_serie: nro_serie
                    };
                    axios.post(this.$urlApi.addEquipoToEstacion, inputs
                    ).then(response => {
                        this.$toastr('success', '', 'TAREA REALIZADA CON ÉXITO');
                        this.getEquiposByEstacion();
                    }).catch(errors => {
                        this.$notifyErrors(errors);
                    });
                } else {
                    let errors=['Seleccione un kit'];
                    this.$notifyErrorsLocal(errors);
                }
            },
            selectedItem(selectedItem) {
                this.selected = [];
                this.estacion = selectedItem;
                this.selected.push(selectedItem);
                this.getEquiposByEstacion();
                this.autocomplete.model=null;
                if(selectedItem && selectedItem.notario_id){
                    this.getNotarioById(selectedItem.notario_id);
                }
            },
            changeEstadoEquipo(equipo){
                let inputs={
                    id_estacion: this.selected[0].id,
                    id_equipo: equipo.id,
                    estado: equipo.estado,
                };
                axios.patch(this.$urlApi.changeEstadoEquipo, inputs
                ).then(response => {
                    this.$toastr('success', '', 'TAREA REALIZADA CON ÉXITO');
                }).catch(errors => {
                    this.$notifyErrors(errors);
                });
            },
            assignNotario(responseNotario){

                let inputs={
                    id_estacion: this.selected[0].id,
                    notario_id: responseNotario ? responseNotario.id : null,
                };
                axios.patch(this.$urlApi.assignNotario, inputs
                ).then(response => {
                    if(responseNotario){
                        this.estacion.notario_id=responseNotario.id;
                        this.estacion.notario=responseNotario;
                    } else {
                        this.estacion.notario=null;
                        this.estacion.notario_id=null;
                    }
                    this.$toastr('success', '', 'TAREA REALIZADA CON ÉXITO');
                }).catch(errors => {
                    this.autocomplete.model=null;
                    this.$notifyErrors(errors);
                });
            },
            getNotarioById(id_notario){
                axios.get(this.$urlApi.resourcesNotario+'/'+id_notario
                ).then(response => {
                    this.autocomplete.model=response.data;
                    this.autocomplete.entries.push(response.data);
                }).catch(errors => {
                    this.$notifyErrors(errors);
                });
            },

            // custom filter to autocomplete
            customFilter: (item, queryText, itemText) => {
                let match=item.nombre.toLocaleLowerCase().indexOf(queryText.toLocaleLowerCase());
                if(match === -1){
                    match=item.ci.toLocaleLowerCase().indexOf(queryText.toLocaleLowerCase());
                }
                return match>-1;
            }
        },
        computed: {
            autocompleteSearch() {
                return this.autocomplete.search;
            },
            fields () {
                if (!this.autocomplete.model) return [];

                return Object.keys(this.autocomplete.model).map(key => {
                    return {
                        key,
                        value: this.autocomplete.model[key] || 'n/a'
                    }
                })
            },
            items () {
                return this.autocomplete.entries.map(entry => {
                    const nombre = entry.nombre.length > this.autocomplete.descriptionLimit
                        ? entry.nombre.slice(0, this.autocomplete.descriptionLimit) + '...'
                        : entry.nombre;
                    return Object.assign({}, entry, { nombre })
                })
            }
        },
        watch: {
            autocompleteSearch (val) {
                // Items have already been loaded
                if (this.items.length > 1) return;

                // Items have already been requested
                if (this.autocomplete.isLoading) return;

                this.autocomplete.isLoading = true;

                // Lazily load input items
                fetch(this.$urlApi.resourcesNotario)
                    .then(res => res.json())
                    .then(res => {
                        // const { count, entries } = res;
                        // console.log(res);
                        this.autocomplete.count = res.length;
                        this.autocomplete.entries = res;
                    })
                    .catch(err => {
                        console.log(err)
                    })
                    .finally(() => (this.autocomplete.isLoading = false))
            }
        }
    }
</script>

<style scoped>
    .invisible{
        visibility: hidden;
    }
