<template>
    <div>
        <v-dialog v-model="dialog" scrollable persistent max-width="1000px">
            <template v-slot:activator="{ on }">
                <v-btn color="info" flat @click="getUsuarios"
                       class="square" v-on="on" title="Usuarios">
                    <v-icon>account_circle</v-icon>
                </v-btn>
            </template>
            <v-card>
                <v-card-title class="grey lighten-2">
                    <v-layout align-center justify-space-between fill-height>
                        <span class="title">Usuarios</span>
                        <div>
                            <v-btn color="info" flat v-if="!modeEdit"
                                   @click="modeCreate=!modeCreate"
                                   class="square">
                                <v-icon>add_box</v-icon>
                            </v-btn>
                            <v-btn color="secondary" flat
                                   @click="dialog=!dialog"
                                   class="square">
                                <v-icon>close</v-icon>
                            </v-btn>
                        </div>
                    </v-layout>
                </v-card-title>
                <v-card-text>
                    <v-container v-if="modeCreate">
                        <v-form ref="formUser" v-model="valid" lazy-validation>
                            <v-text-field
                                    color="blue darken-4"
                                    background-color="blue lighten-5"
                                    box
                                    :rules="rules.usuario"
                                    v-model="usuario.usuario"
                                    label="Nombre de usuario"
                                    required
                            ></v-text-field>
                            <v-text-field
                                    label="Contraseña"
                                    v-model="usuario.contrasenia"
                                    autocomplete="false"
                                    color="blue_darken_1"
                                    background-color="blue lighten-5"
                                    box
                                    :append-icon="show1 ? 'visibility_off' : 'visibility'"
                                    :type="show1 ? 'text' : 'password'"
                                    required
                                    @click:append="show1 = !show1"
                            ></v-text-field>
                            <v-autocomplete
                                    color="blue darken-4"
                                    box
                                    background-color="blue lighten-5"
                                    v-model="searchTecnico.tecnico"
                                    :items="tecnicos"
                                    :loading="searchTecnico.isLoading"
                                    :search-input.sync="inputTecnico"
                                    hide-no-data
                                    hide-selected
                                    item-text="nombre"
                                    item-value="id"
                                    label="Técnico"
                                    placeholder="Comience a escribir para buscar"
                                    return-object
                            ><template v-slot:item="{ item, index }">
                                    <v-layout align-center justify-space-between fill-height>
                                        <v-flex xs8>
                                            <span class="v-text-field--full-width">{{item.nombre }}</span>
                                        </v-flex>
                                        <v-flex class="text-xs-right">
                                            <span>{{item.ci }}</span>
                                        </v-flex>
                                    </v-layout>
                                </template>
                            </v-autocomplete>
                            <div class="flex-center">
                                <v-radio-group v-model.number="usuario.nivel_acceso" style="margin: 0!important;">
                                    <v-layout>
                                        <v-flex>
                                            <v-radio label="Técnico" :value="0"></v-radio>
                                        </v-flex>
                                        <v-flex>
                                            <v-radio label="Auxiliar Técnico" :value="1"></v-radio>
                                        </v-flex>
                                    </v-layout>
                                </v-radio-group>
                            </div>
                        </v-form>
                    </v-container>
                    <v-container v-else>
                        <v-data-table
                            disable-initial-sort
                            :headers="dataTable.headers"
                            :items="dataTable.usuarios"
                    >
                        <template v-slot:items="props">
                            <td>{{ props.item.usuario }}</td>
                            <td>
                                <span v-if="props.item.nivel_acceso===0">Técnico</span>
                                <span v-else>Técnico auxiliar</span>
                            </td>
                            <td>{{ props.item.tecnico ? props.item.tecnico.nombre:'' }}</td>
                            <td width="10%">
                                <v-btn color="info"
                                       @click="editUsuario(props.item)"
                                       flat
                                       class="square"
                                       title="Editar" >
                                    <v-icon>edit</v-icon>
                                </v-btn>
                                <v-btn color="error" flat
                                       title="Quitar"
                                       @click="removeUsuario(props.item)"
                                       class="square">
                                    <v-icon>remove</v-icon>
                                </v-btn>
                            </td>
                        </template>
                    </v-data-table>
                    </v-container>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn v-if="!modeEdit" color="blue darken-1" outline @click="dialog = false">Cerrar</v-btn>
                    <v-btn v-else color="error" outline @click="editUsuarioCancel">Cancelar</v-btn>
                    <v-btn v-if="modeCreate"  color="info" depressed class="text" @click="submit">
                        <span v-if="modeEdit">Actualizar</span>
                        <span v-else>Enviar</span>
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>

<script>
    import Usuario from '../models/Usuario';
    import axios from 'axios';

    export default {
        data: () => ({
            // view
            show1:false,
            dialog: false,
            valid: true,
            modeCreate:false,
            modeEdit:false,
            dataTable: {
                headers: [
                    {
                        text: 'Nombre de usuario',
                        value: 'usuario'
                    },
                    {
                        text: 'Nivel de acceso',
                        value: 'nivel_acceso'
                    },
                    {
                        text: 'Técnico',
                        value: 'tecnico.nombre'
                    },
                    {
                        text: ' ',
                        value:' ',
                        sortable: false,
                    },
                ],
                usuarios: [],
            },
            rules: {
                usuario: [
                    v => !!v || 'Nombre de usuario es requerido',
                    /*v => (v && v.length <= 10) || 'Name must be less than 10 characters'*/
                ],
                contrasenia: [
                    v => !!v || 'Contraseña es requerido',
                    // v => /.+@.+/.test(v) || 'E-mail must be valid'
                ]
            },
            searchTecnico:{
                descriptionLimit: 60,
                entries: [],
                isLoading: false,
                tecnico:null,
                count:null
            },
            inputTecnico:null,
            // model
            usuario: new Usuario(),
        }),
        mounted() {
            // this.getProcesosElectorales();
        },
        computed:{
            tecnicos() {
                return this.searchTecnico.entries.map(entry => {
                    const nombre = entry.nombre.length > this.searchTecnico.descriptionLimit
                        ? entry.nombre.slice(0, this.searchTecnico.descriptionLimit) + '...'
                        : entry.nombre

                    return Object.assign({}, entry, {nombre})
                })
            },
        },
        methods: {
            submit: function () {
                if (this.$refs.formUser.validate()) {
                    if (!this.modeEdit) {
                        this.create()
                    } else {
                        this.update();
                    }
                }
            },
            create() {
                let inputs= {...this.usuario};
                if(this.searchTecnico.tecnico){
                    inputs.tecnico_id=this.searchTecnico.tecnico.id
                }
                axios.post(this.$urlApi.register, inputs
                ).then(response => {
                    this.usuario = new Usuario();
                    this.$toastr('success', '', 'TAREA REALIZADA CON ÉXITO');
                    this.searchTecnico.tecnico=null;
                    this.getUsuarios();
                    // this.$emit('register-success');
                }).catch(errors => {
                    this.$notifyErrors(errors);
                });
            },
            getUsuarios() {
                axios.get(this.$urlApi.resourcesUsuarios
                ).then(response => {
                    this.dataTable.usuarios=response.data;
                }).catch(errors => {

                });

            },
            editUsuario(usuario){
                this.modeEdit=true;
                this.modeCreate=true;
                this.usuario=usuario;
                if(usuario.tecnico){
                    this.searchTecnico.entries.push(usuario.tecnico);
                    this.searchTecnico.tecnico=usuario.tecnico;
                } else {
                    this.searchTecnico.tecnico=null;
                }
            },
            editUsuarioCancel(){
                this.modeEdit=false;
                this.modeCreate=false;
                this.usuario=new Usuario();
                this.searchTecnico.tecnico=null;
            },
            update(){
                let inputs= {...this.usuario};
                if(this.searchTecnico.tecnico){
                    inputs.tecnico_id=this.searchTecnico.tecnico.id
                }
                axios.put(this.$urlApi.resourcesUsuarios+'/'+this.usuario.id,
                    inputs
                ).then(response => {
                    this.$toastr('success', '', 'TAREA REALIZADA CON ÉXITO');
                    this.editUsuarioCancel();
                    this.getUsuarios();
                }).catch(errors => {
                    this.$notifyErrors(errors);
                });
            },
            removeUsuario(usuario) {
                let _confirm = confirm('¿Está seguro?');
                if(_confirm) {
                    axios.delete(this.$urlApi.resourcesUsuarios+'/'+usuario.id
                    ).then(response => {
                        this.getUsuarios();
                        this.$toastr('success', '', 'TAREA REALIZADA CON ÉXITO');
                    }).catch(errors => {
                        console.log('error de servidor')
                        this.$notifyErrors(errors);
                    });
                }
            },
            /*openDialog(){
                Object.assign(this.tecnico,this.editData);
                this.dialog=true;
            }*/
        },
        watch: {
            inputTecnico(val) {
                // Items have already been loaded
                if (this.tecnicos.length > 1) return

                // Items have already been requested
                if (this.searchTecnico.isLoading) return

                this.searchTecnico.isLoading = true

                // Lazily load input items
                fetch(this.$urlApi.resourcesTecnico)
                    .then(res => res.json())
                    .then(res => {

                        this.searchTecnico.count = res
                        this.searchTecnico.entries = res
                    })
                    .catch(err => {
                        console.log(err)
                    })
                    .finally(() => (this.searchTecnico.isLoading = false))
            }
        },
    }
</script>
