export default class Equipo{
    constructor (
        id=null,
        nombre=null,
        marca=null,
        tipo='e',
        codigo_activo=null,
        modelo=null,
        nro_serie=null,
        estado='b',
        created_at=null,
        updated_at=null,
        accesorios=[]
    ) {
        this.id = id;
        this.nombre = nombre;
        this.marca = marca;
        this.tipo = tipo;
        this.codigo_activo = codigo_activo;
        this.modelo = modelo;
        this.nro_serie = nro_serie;
        this.estado = estado;
        this.created_at= created_at;
        this.updated_at= updated_at;
        this.accesorios = accesorios;
    }
}


