export default class Usuario{
    constructor (
        id = null,
        usuario = null,
        contrasenia = null,
        nivel_acceso = 1,
        tecnico = null,
    ) {
        this.id = id;
        this.usuario = usuario;
        this.contrasenia = contrasenia;
        this.nivel_acceso = nivel_acceso;
        this.tecnico = tecnico;
    }
}


