<template>
    <v-content class="main-container">
        <v-toolbar app dense >
            <v-toolbar-title class="headline text-uppercase">
                <!--<span>Vuetify</span>
                <span class="font-weight-light">MATERIAL DESIGN</span>-->
            </v-toolbar-title>
            <v-spacer></v-spacer>
            <v-icon>mdi_anchor</v-icon>
            <v-toolbar-items>
                <!--<v-menu offset-y>
                  <template v-slot:activator="{ on }">
                    <v-btn v-on="on" flat>
                      <v-icon left>people</v-icon>
                      Personal
                    </v-btn>
                  </template>
                  <v-list >
                    <v-list-tile to="/personal/tecnicos" active-class="active-custom">
                      <v-list-tile-title
                      >Tecnicos</v-list-tile-title>
                    </v-list-tile>
                    <v-list-tile>
                      <v-list-tile-title>Notarios</v-list-tile-title>
                    </v-list-tile>
                  </v-list>
                </v-menu>-->

                <v-btn to="/personal" flat>
                    <v-icon left>people</v-icon>
                    Personal
                </v-btn>
                <v-btn to="/equipos" flat>
                    <v-icon left>laptop_windows</v-icon>
                    Equipos
                </v-btn>
                <v-btn to="/pro-ele-ref" flat title="Proceso Electoral o Referendo">
                    <v-icon left>how_to_vote</v-icon>
                    Pro. Ele. Ref.
                </v-btn>
                <v-btn to="/asignaciones" flat>
                    <v-icon left>assignment_ind</v-icon>
                    Asignaciones
                </v-btn>
                <v-menu bottom offset-y nudge-bottom="10" v-if="user">
                    <template v-slot:activator="{ on }">
                        <v-btn small color="indigo"
                               dark
                               icon
                               v-on="on"
                        >
                            <span class="headline" style="line-height: initial !important;">{{user.usuario | extractFirstLetter}}</span>
                        </v-btn>
                    </template>
                    <v-list>
                        <v-list-tile @click="logout">
                            <v-list-tile-content>
                                <v-list-tile-title>Cerrar Sesión</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </v-list>
                </v-menu>
                <!--<v-btn to="/" flat>
                  <v-icon left>laptop_windows</v-icon>
                  Equipos
                </v-btn>-->
                <!--<v-btn flat>Link Two</v-btn>
                <v-btn flat>Link Three</v-btn>-->
            </v-toolbar-items>
        </v-toolbar>
        <span class="font-weight-bold"></span>
        <transition name="fade" mode="out-in">
            <router-view/>
        </transition>
    </v-content>
</template>
<script>

    import { mapMutations, mapState } from 'vuex';
    import router from '../router';
    export default {
        name: 'AppContainer',
        data () {
            return {
                //
            }
        },
        methods:{
            ...mapMutations(['storeAuthData']),
            logout(){
                this.$http.get(this.$urlApi.logout
                ).then(response=>{
                    let data={
                        user:null,
                        token:null,
                    };
                    this.storeAuthData(data);
                    router.replace('login');
                }, err=>{
                    if(err.response.status === 401){
                        let data={
                            user:null,
                            token:null,
                        };
                        this.storeAuthData(data);
                        router.replace('login');
                    } else {
                        console.log('errror logout');
                    }
                });
            }
        },
        computed:{
            ...mapState(['user'])
        }
    }
</script>
<style scoped>
    .fade-enter-active, .fade-leave-active {
        transition: opacity .3s ease;
    }
    .fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }
</style>